.chapter {
  padding: 25px 0 30px;
  &__top {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    gap: 4px 16px;
  }
  &__subtitle {
    font-size: 20px;
    line-height: 130%;
    font-weight: 600;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    margin-top: 30px;
  }
  &__slider {
    position: relative;
    margin-top: 24px;
  }
  @media screen and (min-width: 993px) {
    .swiper-slide {
      width: calc(100% / 3) !important;
    }
  }
}
.chapter-card {
  display: flex;
  align-items: center;
  background: #F9EEE2;
  &__img {
    width: 115px;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 14px;
    line-height: 140%;
    font-weight: 500;
    letter-spacing: 0.2px;
    text-transform: uppercase;
    padding: 20px;
  }
}
.catalog {
  padding: 30px 0;
  &__menu {
    .open-more {
      display: inline-flex;
      align-items: center;
      gap: 8px;
      background: #F9EEE2;
      padding: 6px 12px;
      cursor: pointer;
      margin-top: 10px;
      span {
        font-size: 14px;
        line-height: 120%;
        color: #111111;
      }
      span:nth-child(2) {
        display: none;
      }
      i {
        width: 6px;
        display: flex;
        align-items: center;
        transition: .3s;
        img {
          width: 100%;
        }
      }
      &--active {
        span:nth-child(1) {
          display: none;
        }
        span:nth-child(2) {
          display: block;
        }
        i {
          transform: rotate(-180deg);
        }
      }
    }
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      transform: unset;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 12px;
    }
    .swiper-slide {
      margin: 0 !important;
      &:nth-child(10) ~ .swiper-slide {
        display: none;
      }
    }
  }
  &__link {
    padding: 6px 12px;
    span {
      font-size: 14px;
      text-transform: unset;
    }
  }
  .swiper-slide {
    width: auto !important;
  }
  .more-link {
    display: flex !important;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    margin-top: 30px;
  }
  &__content {
    width: calc(100% - 245px);
  }
  &__content-nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 20px;
  }
  .filter-btn {
    display: none;
    font-size: 14px;
    line-height: 120%;
    cursor: pointer;
    &:hover {
      color: #b88c5e;
    }
  }
  .nice-select {
    width: auto;
    display: flex;
    align-items: center;
    background: transparent;
    border: none;
    box-shadow: unset;
    padding: 0 30px 0 0;
    font-size: 14px;
    line-height: 120%;
    &:after {
      top: 50%;
      right: 0;
      transform: translate(0, -50%) rotate(0);
      width: 9px;
      height: 6px;
      background-image: url("../img/arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    .list {
      width: 215px;
      background: #fff;
      box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.15);
      padding: 12px 24px;
      li {
        background: transparent !important;
        border: none;
        padding: 12px 0;
        font-size: 14px;
        line-height: 120%;
        font-weight: 400 !important;
        &:hover {
          color: #B88C5E;
        }
        &:focus {
          background: transparent;
          color: #B88C5E;
        }
      }
    }
  }
  .open {
    &:after {
      transform: translate(0, -50%) rotate(-180deg);
    }
  }
  .sorting-cards {
    display: flex;
    align-items: center;
    gap: 20px;
    .active {
      svg path {
        fill: #111111;
      }
    }
  }
  .sorting-card {
    max-width: 21px;
    cursor: pointer;
    svg {
      max-width: 100%;
    }
    svg:nth-child(2) {
      display: none;
    }
  }
  &__cards {
    display: grid;
    gap: 30px;
    margin-top: 30px;
  }
  .sorting-cards--1 {
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
  }
  .sorting-cards--2 {
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
  }
  .pagination {
    margin-top: 30px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__item {
    &:nth-child(1) {
      .catalog__item-hidden {
        display: block;
      }
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 3px 0;
      cursor: pointer;
    }
    .item-show--active {
      .catalog__item-btn {
        transform: rotate(-180deg);
      }
    }
    &-title {
      font-size: 14px;
      line-height: 120%;
      font-weight: 700;
    }
    &-btn {
      width: 6px;
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
    &-hidden {
      display: none;
      margin-top: 20px;
    }
    &-wrap {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
    }
    &-group {
      &:nth-child(1) {
        .catalog__item-hidden2 {
          display: block;
        }
      }
    }
    &-show2 {
      font-size: 14px;
      line-height: 120%;
      cursor: pointer;
      &:hover {
        color: #B88C5E;
      }
    }
    &-hidden2 {
      display: none;
      margin-top: 16px;
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      padding-left: 16px;
      li {
        font-size: 14px;
        line-height: 120%;
        &:hover {
          color: #B88C5E;
        }
      }
      li:nth-child(9) ~ li {
        display: none;
      }
      .active {
        font-weight: 600;
        color: #B88C5E;
      }
    }
    .more-list--active {
      li {
        display: block !important;
      }
      .catalog__item-checkbox {
        display: flex !important;
      }
    }
    .open-more {
      display: inline-flex;
      align-items: center;
      gap: 8px;
      cursor: pointer;
      margin-top: 16px;
      span {
        font-size: 14px;
        line-height: 120%;
        color: #B88C5E;
      }
      span:nth-child(2) {
        display: none;
      }
      i {
        width: 6px;
        display: flex;
        align-items: center;
        transition: .3s;
        img {
          width: 100%;
        }
      }
      &--active {
        span:nth-child(1) {
          display: none;
        }
        span:nth-child(2) {
          display: block;
        }
        i {
          transform: rotate(-180deg);
        }
      }
    }
    &-search {
      position: relative;
      input {
        width: 100%;
        height: 34px;
        border: none;
        border-bottom: 1px solid #111111;
        padding: 0 0 0 34px;
        font-size: 14px;
        line-height: 120%;
        color: #111111;
        &::placeholder {
          color: rgba(17, 17, 17, 0.4);
        }
      }
    }
    .catalog__search-btn {
      position: absolute;
      top: 50%;
      left: 10px;
      transform: translate(0, -50%);
      width: 15px;
      background: transparent;
      padding: 0;
      &:hover {
        svg path {
          stroke: #111111;
        }
      }
      svg {
        width: 100%;
        height: auto;
        path {
          transition: all 0.3s;
        }
      }
    }
    &-checkboxes {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 20px;
    }
    &-checkbox {
      position: relative;
      display: flex;
      align-items: center;
      &:nth-child(6) ~ .catalog__item-checkbox {
        display: none;
      }
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        cursor: pointer;
        opacity: 0;
      }
      input:checked {
        label {
          &:after {
            background-image: url("../img/check.svg");
          }
        }
      }
      label {
        position: relative;
        font-size: 14px;
        line-height: 120%;
        padding-left: 30px;
        &:after {
          content: '';
          position: absolute;
          top: -3px;
          left: 0;
          width: 20px;
          height: 20px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: auto;
          border: 1px solid #111111;
          transition: all 0.3s;
        }
      }
    }
  }
  .filters-price__inputs {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 30px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 10px;
      height: 1px;
      background: #000;
    }
  }
  .filters-price__label {
    position: relative;
    input {
      width: 100%;
      height: 37px;
      background: transparent;
      border: none;
      border-bottom: 1px solid #111111;
      padding: 0 0 0 ;
      font-size: 14px;
      line-height: 120%;
      color: #111111;
      &::placeholder {
        color: rgba(17, 17, 17, 0.4);
      }
    }
  }
  .filters-price__slider {
    margin-top: 27px;
  }
  .noUi-target {
    height: 2px;
    background: transparent;
    border: none;
    box-shadow: unset;
  }
  .noUi-connect {
    background: rgba(220, 197, 171, 1);
  }
  .noUi-connects {
    background: rgba(220, 197, 171, 0.25);
  }
  .noUi-handle {
    width: 16px;
    height: 16px;
    border: none;
    background: #DCC5AB;
    box-shadow: unset;
    border-radius: 50%;
    &:after,
    &:before {
      display: none;
    }
  }
  .noUi-horizontal .noUi-handle {
    right: -8px;
  }
}
.catalog-sidebar {
  width: 215px;
  flex-shrink: 0;
  &__top {
    display: none;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    margin-bottom: 20px;
  }
  &__title {
    font-size: 20px;
    line-height: 120%;
    font-weight: 600;
  }
  &__close {
    width: 18px;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
    }
  }
}
.categories2 {
  padding: 30px 0;
  &__top {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    gap: 4px 16px;
  }
  &__slider-btns {
    flex-shrink: 0;
    display: inline-flex;
    align-items: center;
    gap: 30px;
  }
  &__slider {
    position: relative;
    margin-top: 24px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    margin-top: 0;
    &:after {
      font-size: 30px;
      color: #111111;
    }
  }
  .swiper-pagination {
    display: none;
  }
}
.category2-card {
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: #F9EEE2;
  padding: 30px;
  &__title {
    font-size: 16px;
    line-height: 140%;
    font-weight: 500;
    letter-spacing: 0.3px;
    text-transform: uppercase;
  }
}
.banner2 {
  color: #fff;
  padding: 30px 0;
  &__inner {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 100px;
  }
  &__title {
    max-width: 500px;
    font-size: 38px;
    line-height: 130%;
    letter-spacing: 0.5px;
    text-transform: uppercase;
  }
  &__btn {
    padding: 12px 18px;
    margin-top: 24px;
    span {
      font-size: 16px;
    }
  }
}
.portfolio2 {
  padding: 30px 0;
  &__top {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    gap: 4px 16px;
  }
  &__slider-btns {
    flex-shrink: 0;
    display: inline-flex;
    align-items: center;
    gap: 30px;
  }
  &__slider {
    position: relative;
    margin-top: 24px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    margin-top: 0;
    &:after {
      font-size: 30px;
      color: #111111;
    }
  }
  .swiper-pagination {
    display: none;
  }
  .portfolio-card {
    height: auto;
    &__img {
      flex-grow: unset;
      padding-bottom: 80% !important;
    }
    &__text {
      flex-grow: 1;
    }
  }
  &__btn {
    padding: 11.5px 16px;
    margin: 30px auto 0;
    span {
      font-size: 14px;
    }
  }
}


@media screen and (max-width: 992px) {
  .chapter {
    padding: 15px 0 25px;
    &__subtitle {
      margin-top: 20px;
    }
    &__slider {
      margin-top: 20px;
    }
    .swiper {
      overflow: unset;
    }
  }
  .chapter-card {
    &__title {
      font-size: 13px;
      padding: 20px 12px;
    }
  }
  .catalog {
    padding: 25px 0;
    &__menu {
      .swiper {
        overflow: unset;
      }
      .open-more {
        display: none;
      }
    }
    &__wrap {
      margin-top: 20px;
    }
    &__content {
      width: 100%;
    }
    &__content-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 20px;
    }
    .filter-btn {
      display: block;
    }
    &__cards {
      gap: 15px;
      margin-top: 10px;
    }
    .sorting-cards--1 {
      grid-template-columns: repeat(4, calc(25% - 45px / 4));
    }
    .sorting-cards--2 {
      grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
    }
    .pagination {
      margin-top: 20px;
    }
  }
  .catalog-sidebar {
    position: fixed;
    z-index: 20;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    padding: 20px 15px 50px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    overflow-y: auto;
    &--active {
      opacity: 1;
      pointer-events: auto;
    }
    &__top {
      display: flex;
    }
  }
  .categories2 {
    padding: 25px 0;
    &__slider {
      padding-bottom: 20px;
    }
    .swiper {
      overflow: unset;
    }
    .swiper-wrapper {
      flex-direction: row;
    }
    .swiper-slide {
      margin: 0 10px 10px 0 !important;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      top: unset;
      bottom: 0;
      display: block;
    }
  }
  .category2-card {
    padding: 12px 20px;
    &__title {
      font-size: 14px;
    }
  }
  .banner2 {
    padding: 25px 0;
    &__inner {
      padding: 100px 25px;
    }
    &__title {
      font-size: 24px;
    }
  }
  .portfolio2 {
    padding: 25px 0;
    &__slider {
      padding-bottom: 30px;
      margin-top: 24px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      top: unset;
      bottom: 0;
      display: block;
    }
    &__btn {
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 768px) {
  .catalog {
    &__cards {
      gap: 10px;
    }
    .sorting-cards--1 {
      grid-template-columns: repeat(4, calc(25% - 30px / 4));
    }
    .sorting-cards--2 {
      grid-template-columns: repeat(3, calc(100% / 3 - 20px / 3));
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog {
    .sorting-card {
      svg:nth-child(1) {
        display: none;
      }
      svg:nth-child(2) {
        display: block;
      }
    }
    .sorting-cards--1 {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
    .sorting-cards--2 {
      grid-template-columns: 100%;
    }
  }
  .banner2 {
    &__inner {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      padding: 25px 15px 280px;
    }
  }
}