.footer {
  background: #111111;
  color: #fff;
  &__group {
    border-bottom: 1px solid #262626;
    padding: 36px 0;
  }
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
    li a {
      font-size: 14px;
      line-height: 120%;
      font-weight: 300;
      letter-spacing: .2px;
      text-transform: uppercase;
      &:hover {
        color: #FFAF58;
      }
    }
  }
  .flex-group {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__time {
    position: relative;
    font-size: 14px;
    line-height: 120%;
    font-weight: 300;
    padding-right: 16px;
    margin-right: 16px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 1px;
      height: 17px;
      background: #303030;
    }
  }
  &__phone {
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    margin-right: 40px;
    &:hover {
      color: #FFAF58;
    }
  }
  &__payment {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin-top: 18px;
    &-wrap {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &-title {
      font-size: 14px;
      line-height: 120%;
      font-weight: 300;
      letter-spacing: .2px;
      text-transform: uppercase;
    }
    &-method {
      max-width: 64px;
      img {
        max-width: 100%;
      }
    }
    &-text {
      font-size: 12px;
      line-height: 120%;
      margin-top: 18px;
      a {
        text-decoration: underline;
        text-underline-offset: 3px;
        &:hover {
          color: #FFAF58;
          text-decoration-color: #FFAF58;
        }
      }
    }
  }
  &__politic {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    line-height: 120%;
    text-decoration: underline;
    text-underline-offset: 3px;
    &:hover {
      color: #FFAF58;
      text-decoration-color: #FFAF58;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__group {
      padding: 30px 0;
    }
    &__list {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 10px;
    }
    .flex-group {
      flex-direction: column;
      justify-content: start;
      align-items: start;
    }
    &__time {
      order: 1;
      padding-right: 0;
      margin-right: 0;
      margin-top: 10px;
    }
    &__payment {
      flex-wrap: wrap;
      justify-content: start;
      gap: 15px;
      &-wrap {
        align-items: start;
      }
    }
  }
}