.main {
  color: #fff;
  &__slider {
    position: relative;
  }
  .swiper {
    overflow: unset;
  }
  &-slide {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 156px 0 215px;
  }
  &__group {
    max-width: 620px;
  }
  &__title {
    font-size: 38px;
    line-height: 130%;
    font-weight: 400;
    letter-spacing: 0.5px;
    text-transform: uppercase;
  }
  &__text {
    max-width: 535px;
    margin-top: 14px;
    p {
      font-size: 20px;
      line-height: 125%;
    }
  }
  &__btn {
    padding: 12px 18px;
    margin-top: 43px;
    span {
      font-size: 16px;
    }
  }
  .swiper-button-prev {
    left: 40px;
  }
  .swiper-button-next {
    right: 40px;
  }
  .swiper-pagination {
    bottom: -28px;
    &-bullet {
      background: #F9E2C8;
    }
    &-bullet-active {
      background: #B88C5E;
    }
  }
}
.directions {
  padding: 88px 0 30px;
  &__tabs-head {
    margin-top: 50px;
    .swiper {
      overflow: unset;
    }
  }
  &__tab {
    width: auto !important;
    font-size: 14px;
    line-height: 120%;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    text-decoration: underline;
    text-decoration-thickness: 2px;
    text-underline-offset: 5px;
    text-decoration-color: transparent;
    cursor: pointer;
  }
  .tab--active {
    color: #B88C5E;
    text-decoration-color: #B88C5E;
  }
  &__tabs-body {
    margin-top: 40px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
  }
  .tab__content {
    display: none;
    &--active {
      display: flex;
    }
  }
  &__left {
    width: 48%;
    padding-top: 16px;
  }
  &__right {
    width: 52%;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 100%;
  }
  &__list {
    max-width: 400px;
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 20px;
    li {
      position: relative;
      font-size: 16px;
      line-height: 150%;
      padding-left: 36px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        border: 10px solid transparent;
        border-left: 16px solid #B88C5E;
      }
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 15px / 2));
    gap: 15px;
    margin-top: 40px;
  }
  &__btn {
    padding: 11.5px 4px;
    span {
      font-size: 14px;
    }
  }
  &__slider {
    position: relative;
    .swiper-slide {
      img {
        width: 100%;
      }
    }
  }
  .swiper-button-prev {
    left: 18px;
    &:after {
      font-size: 40px;
    }
  }
  .swiper-button-next {
    right: 18px;
    &:after {
      font-size: 40px;
    }
  }
  .swiper-pagination {
    bottom: 30px;
    &-bullet {
      background: rgba(255, 255, 255, 0.4);
    }
    &-bullet-active {
      background: #F9E2C8;
    }
  }
  &__text {
    margin-top: 12px;
    p {
      text-align: center;
      font-size: 14px;
      line-height: 150%;
    }
  }
}
.advantages {
  padding: 30px 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25%));
    margin-top: 30px;
  }
  &__item {
    padding: 50px 18px;
    &:nth-child(odd) {
      background: #F9EEE2;
    }
    &-title {
      display: flex;
      align-items: center;
      gap: 8px;
      font-size: 14px;
      line-height: 160%;
      font-weight: 500;
      letter-spacing: 0.2px;
      text-transform: uppercase;
      span {
        flex-shrink: 0;
        font-size: 50px;
        line-height: 100%;
        font-weight: 400;
        letter-spacing: 0.5px;
      }
    }
    &-text {
      font-size: 14px;
      line-height: 150%;
      color: #555555;
      margin-top: 5px;
    }
  }
}
.products {
  padding: 30px 0;
  &__top {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    gap: 4px 16px;
    padding-right: 100px;
  }
  &__slider-btns {
    position: absolute;
    top: -74px;
    right: 0;
    flex-shrink: 0;
    display: inline-flex;
    align-items: center;
    gap: 30px;
    &2 {
      top: -140px;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    margin-top: 0;
    &:after {
      font-size: 30px;
      color: #111111;
    }
  }
  .tab--active {
    color: #B88C5E;
    text-decoration-color: #B88C5E;
  }
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  .swiper {
    z-index: 2;
    overflow: unset;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    &-active,
    &-active + .swiper-slide,
    &-active + .swiper-slide + .swiper-slide,
    &-active + .swiper-slide + .swiper-slide + .swiper-slide {
      opacity: 1;
      pointer-events: auto;
    }
  }
  .swiper-pagination {
    z-index: 1;
    bottom: -28px;
    &-bullet {
      background: #F9E2C8;
    }
    &-bullet-active {
      background: #B88C5E;
    }
  }
  &__btn {
    padding: 11.5px 16px;
    margin: 50px auto 0;
    span {
      font-size: 14px;
    }
  }
}
.product-card {
  position: relative;
  background: #fff;
  border: 1px solid #E6E6E6;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% + 110px);
    background: #fff;
    box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.15);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &:hover {
    border-color: transparent;
    &:after {
      opacity: 1;
    }
    .hover__pag {
      opacity: 1;
    }
    .product-card__btn-hover {
      opacity: 1;
    }
    .product-card__info {
      opacity: 1;
    }
    .product-card__btns {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__top {
    position: relative;
    z-index: 1;
  }
  &__btn-hover {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: max-content;
    padding: 7.5px 15px;
    opacity: 0;
    transition: all 0.3s;
    span {
      font-size: 12px;
    }
  }
  &__desc {
    position: relative;
    z-index: 1;
    padding: 10px 15px;
  }
  &__title {
    position: relative;
    font-size: 14px;
    line-height: 120%;
    white-space: nowrap;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:1;
    -webkit-box-orient: vertical;
    &:hover {
      color: #b88c5e;
    }
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 16px;
      height: 16px;
      background: linear-gradient(270.00deg, rgb(255, 255, 255),rgba(255, 255, 255, 0.1) 100%);
    }
  }
  &__price {
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 5px;
  }
  &__info {
    font-size: 12px;
    line-height: 120%;
    margin-top: 5px;
    opacity: 0;
    span {
      color: #AEAEAE;
    }
  }
  &__btns {
    position: absolute;
    z-index: 2;
    top: 100%;
    left: 0;
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    padding: 5px 15px 15px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &__btn {
    width: 100%;
    height: 40px;
    padding: 12px;
    span {
      font-size: 13px;
    }
  }
}
.categories {
  padding: 30px 0;
  &__slider {
    position: relative;
    margin-top: 50px;
  }
  &__item {
    display: grid;
    grid-template-columns: 100%;
    gap: 25px;
    &-link {
      font-size: 14px;
      line-height: 120%;
      &:hover {
        color: #b88c5e;
      }
    }
  }
  .swiper-pagination {
    display: none;
  }
  &__btn {
    padding: 11.5px 16px;
    margin: 24px auto 0;
    span {
      font-size: 14px;
    }
  }
}
.banner {
  padding: 30px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 15px solid #F9EEE2;
  }
  &__img {
    max-width: 400px;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__desc {
    max-width: 760px;
    padding: 40px 65px;
  }
  &__title {
    font-size: 36px;
    line-height: 125%;
    letter-spacing: 0.5px;
    text-transform: uppercase;
  }
  &__btn {
    padding: 11.5px 16px;
    margin-top: 22px;
    span {
      font-size: 14px;
    }
  }
}
.portfolio {
  padding: 30px 0;
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      transform: unset !important;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
      gap: 30px;
    }
    .swiper-slide {
      width: 100% !important;
    }
    .swiper-pagination {
      display: none;
    }
  }
  &__btn {
    padding: 11.5px 16px;
    margin: 30px auto 0;
    span {
      font-size: 14px;
    }
  }
}
.portfolio-card {
  display: flex;
  flex-direction: column;
  &:nth-child(1) {
    grid-row: 1/6;
    @media screen and (min-width: 993px) {
      .portfolio-card__img {
        padding-bottom: 80%;
      }
    }
  }
  &:nth-child(2) {
    grid-row: 1/4;
  }
  &:nth-child(3) {
    grid-row: 6/12;
    @media screen and (min-width: 993px) {
      .portfolio-card__img {
        padding-bottom: 80%;
      }
    }
  }
  &:nth-child(4) {
    grid-row: 4/8;
  }
  &:nth-child(5) {
    grid-row: 8/12;
  }
  &__img {
    position: relative;
    flex-grow: 1;
    padding-bottom: 48%;
    overflow: hidden;
    &:hover {
      img {
        transform: scale(1.2);
      }
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: all 1.5s;
    }
  }
  &__text {
    background: #F9EEE2;
    padding: 24px;
    p {
      font-size: 16px;
      line-height: 150%;
      font-weight: 500;
    }
  }
}
.about {
  padding: 30px 0;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
    border: 15px solid #F9EEE2;
    padding: 45px;
    margin-top: 30px;
  }
  &__img {
    position: relative;
    width: 260px;
    flex-shrink: 0;
    padding-right: 50px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 2px;
      height: 165px;
      background: #B88C5E;
    }
    img {
      width: 100%;
    }
  }
  &__text {
    p {
      font-size: 16px;
      line-height: 160%;
    }
  }
  &__btn {
    padding: 11.5px 16px;
    margin-top: 20px;
    span {
      font-size: 14px;
    }
  }
}
.connection {
  background: #F9EEE2;
  padding: 100px 0;
  margin-top: 30px;
  &__inner {
    max-width: 765px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
  }
  &__title {
    text-align: center;
    text-transform: none;
  }
  &-form {
    margin-top: 30px;
    &__wrap {
      display: flex;
      justify-content: center;
      align-items: stretch;
      gap: 15px;
    }
    &__group {
      input {
        width: 300px;
        height: 40px;
        background: transparent;
        border: none;
        border-bottom: 1px solid #111111;
        padding: 0 10px;
        font-size: 14px;
        line-height: 120%;
        color: #111111;
        &::placeholder {
          color: rgba(17, 17, 17, 0.4);
        }
      }
    }
    &__btn {
      flex-shrink: 0;
      padding: 11.5px 16px;
      span {
        font-size: 14px;
      }
    }
    &__check {
      font-size: 11px;
      line-height: 120%;
      margin-top: 15px;
      a {
        text-decoration: underline;
        text-underline-offset: 3px;
        &:hover {
          color: #b88c5e;
          text-decoration-color: #b88c5e;
        }
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .main {
    &-slide {
      padding: 156px 36px 215px;
    }
    .swiper-button-prev {
      left: 10px;
    }
    .swiper-button-next {
      right: 10px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .advantages {
    @media screen and (min-width: 993px) {
      &__item {
        &-title {
          span {
            font-size: 40px;
          }
        }
      }
    }
  }
  .banner {
    &__desc {
      padding: 40px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    .container {
      padding: 0;
    }
    &-slide {
      padding: 364px 0 0 0;
    }
    &__group {
      max-width: 100%;
      background: rgba(0, 0, 0, 0.68);
      backdrop-filter: blur(13.6px);
      padding: 20px 15px;
    }
    &__title {
      font-size: 24px;
    }
    &__text {
      max-width: 100%;
      p {
        font-size: 14px;
      }
    }
    &__btn {
      margin-top: 14px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      &:after {
        font-size: 32px;
      }
    }
  }
  .directions {
    padding: 70px 0 25px;
    &__tabs-head {
      margin-top: 30px;
    }
    &__tabs-body {
      margin-top: 30px;
    }
    &__content {
      flex-direction: column;
      gap: 20px;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      order: -1;
    }
    &__subtitle {
      font-size: 16px;
    }
    .swiper-button-prev {
      left: 15px;
    }
    .swiper-button-next {
      right: 15px;
    }
    .swiper-pagination {
      bottom: 12px;
    }
    &__text {
      p {
        font-size: 12px;
      }
    }
  }
  .advantages {
    padding: 25px 0;
    &__items {
      grid-template-columns: repeat(2, calc(50%));
      margin-top: 20px;
    }
    &__item {
      padding: 18px;
    }
  }
  .categories {
    padding: 25px 0;
    &__slider {
      padding-bottom: 30px;
      margin-top: 30px;
    }
    .swiper {
      overflow: unset;
    }
    &__item {
      gap: 20px;
    }
    .swiper-pagination {
      top: unset;
      bottom: 0;
      display: block;
    }
    &__btn {
      margin-top: 16px;
    }
  }
  .products {
    padding: 25px 0;
    &__slider-btns {
      display: none;
    }
    &__slider {
      padding-bottom: 30px;
      margin-top: 20px;
    }
    .swiper-slide {
      opacity: 1;
      pointer-events: auto;
    }
    .swiper-pagination {
      top: unset;
      bottom: 0;
    }
  }
  .banner {
    padding: 25px 0;
    &__inner {
      flex-direction: column;
      gap: 22px;
      padding: 32px 16px 16px;
    }
    &__desc {
      order: -1;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      padding: 0;
    }
    &__title {
      font-size: 24px;
    }
  }
  .portfolio {
    padding: 25px 0;
    &__slider {
      padding-bottom: 30px;
    }
    .swiper {
      overflow: unset;
    }
    .swiper-wrapper {
      align-items: stretch;
    }
    .swiper-slide {
      height: auto;
    }
    .swiper-pagination {
      top: unset;
      bottom: 0;
    }
    &__btn {
      margin-top: 20px;
    }
  }
  .portfolio-card {
    &__img {
      flex-grow: unset;
      padding-bottom: 100%;
    }
    &__text {
      flex-grow: 1;
      padding: 15px;
      p {
        font-size: 14px;
      }
    }
  }
  .about {
    padding: 25px 0;
    &__wrap {
      gap: 30px;
      padding: 32px 16px;
      margin-top: 20px;
    }
    &__img {
      width: 210px;
      padding-right: 30px;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
  }
  .connection {
    padding: 50px 0;
    margin-top: 25px;
    &-form {
      max-width: 306px;
      &__wrap {
        flex-direction: column;
      }
      &__group {
        input {
          width: 100%;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .about {
    &__wrap {
      flex-direction: column;
    }
    &__img {
      width: 100%;
      max-width: 200px;
      padding-right: 0;
      padding-bottom: 30px;
      &:after {
        width: 165px;
        height: 2px;
        top: unset;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
      }
    }
    &__desc {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    .swiper-button-prev,
    .swiper-button-next {
      top: 40%;
    }
  }
  .directions {
    &__btns {
      grid-template-columns: 100%;
    }
  }
  .advantages {
    &__items {
      grid-template-columns: 100%;
    }
  }
}