.wrapper {
  overflow: hidden;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-white__outline {
  background: transparent;
  border: 1px solid #fff;
  &:hover {
    border-color: #DEB182;
    span {
      color: #DEB182;
    }
  }
  span {
    line-height: 120%;
    font-weight: 500;
    letter-spacing: 0.2px;
    text-transform: uppercase;
    color: #fff;
    transition: all 0.3s;
  }
}
.btn-black {
  gap: 10px;
  background: #111111;
  &:hover {
    span {
      color: #DEB182;
    }
    .arrow path {
      fill: #DEB182;
    }
  }
  span {
    line-height: 120%;
    font-weight: 500;
    letter-spacing: 0.2px;
    text-transform: uppercase;
    color: #fff;
    transition: all 0.3s;
  }
  .arrow {
    width: 18px;
    svg {
      width: 100%;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }

}
.btn-black__outline {
  gap: 10px;
  background: transparent;
  border: 1px solid #111111;
  &:hover {
    border-color: #DEB182;
    span {
      color: #DEB182;
    }
    .arrow path {
      fill: #DEB182;
    }
  }
  span {
    line-height: 120%;
    font-weight: 500;
    letter-spacing: 0.2px;
    text-transform: uppercase;
    color: #111111;
    transition: all 0.3s;
  }
  .arrow {
    width: 18px;
    svg {
      width: 100%;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
}
.swiper-button-prev,
.swiper-button-next {
  &:after {
    color: #fff;
  }
}
.swiper-pagination {
  &-bullet {
    width: 8px;
    height: 8px;
    opacity: 1;
    margin: 0 7px !important;
  }
}
.swiper-pagination-progressbar {
  height: 2px !important;
  background: #F9EEE2;
  &-fill {
    background: #111111 !important;
  }
}
.h2 {
  font-size: 28px;
  line-height: 130%;
  font-weight: 400;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}
.hover {
  &__slide {
    position: relative;
    padding: 24px 15px 0;
  }
  &__img {
    position: relative;
    display: none;
    padding-bottom: 100%;
    overflow: hidden;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    &--active {
      display: block;
    }
  }
  &__pag {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    gap: 4px;
    padding: 15px 15px 0;
    opacity: 0;
    &-item {
      background-color: transparent;
      padding: 0;
      width: 100%;
      max-width: calc(100% / 7);
      flex-grow: 1;
      height: 100%;
      border-top: 2px solid #F9EEE2;
      &--active {
        border-color: #DEB182;
      }
    }
  }
}
.breadcrumbs {
  padding: 25px 0;
  .swiper {
    overflow: unset;
  }
  &__item {
    position: relative;
    width: auto !important;
    font-size: 12px;
    line-height: 120%;
    &:not(:last-child) {
      padding-right: 12px;
      &:after {
        content: '';
        position: absolute;
        top: 5px;
        right: -4px;
        border: 3px solid transparent;
        border-left: 6px solid #111111;
      }
    }
    &:hover,
    &--active {
      color: #9E9E9E;
      &:after {
        border-left-color: #9E9E9E;
      }
    }
  }
}
.quantity {
  font-size: 14px;
  line-height: 120%;
}
.pagination {
  display: flex;
  align-items: stretch;
  gap: 20px;
  &__items {
    display: flex;
    align-items: center;
    gap: 5px;
  }
  &__item {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    line-height: 105%;
  }
  .active {
    background: #000;
    color: #fff;
  }
  &__prev,
  &__next {
    padding: 11.5px 16px;
    span {
      font-size: 14px;
    }
  }
}


@media screen and (max-width: 992px) {
  .h2 {
    font-size: 22px;
  }
  .breadcrumbs {
    padding: 90px 0 15px;
  }
  .pagination {
    gap: 10px;
    &__item {
      width: 33px;
      height: 33px;
      font-size: 14px;
      &:nth-child(4),
      &:nth-child(5),
      &:nth-child(6),
      &:nth-child(7),
      &:nth-child(8) {
        display: none;
      }
    }
    &__prev,
    &__next {
      padding: 10px 16px;
    }
  }
}

@media screen and (max-width: 359px) {
  .pagination {
    &__item {
      width: 28px;
      height: 28px;
      font-size: 12px;
    }
    &__prev,
    &__next {
      padding: 6px 12px;
      span {
        font-size: 12px;
      }
    }
  }
}