.header-top {
  background: #111111;
  border-bottom: 1px solid #262626;
  padding: 11px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 20px;
    li a {
      font-size: 14px;
      line-height: 120%;
      font-weight: 300;
      color: #F9EEE2;
      &:hover {
        color: #FFAF58;
      }
    }
  }
  &__group {
    display: flex;
    align-items: center;
  }
  &__time {
    position: relative;
    font-size: 14px;
    line-height: 120%;
    font-weight: 300;
    color: #fff;
    padding-right: 16px;
    margin-right: 16px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 1px;
      height: 17px;
      background: #303030;
    }
  }
  &__phone {
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    color: #fff;
    margin-right: 40px;
    &:hover {
      color: #FFAF58;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 18px;
    margin-right: 40px;
  }
  &__social {
    width: 24px;
    &:hover {
      svg path {
        fill: #FFAF58;
      }
    }
    svg {
      width: 100%;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
  &__btn {
    background: transparent;
    padding: 0;
    &:hover {
      span {
        color: #FFAF58;
      }
    }
    span {
      font-size: 14px;
      line-height: 120%;
      text-transform: uppercase;
      letter-spacing: 0.2px;
      color: #fff;
      transition: all 0.3s;
    }
  }
  .header-bottom__list {
    display: none;
  }
  .search {
    display: none;
  }
  .header-center__btn {
    display: none;
  }
}
.header-center {
  background: #111111;
  padding: 20px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
  }
  &__logo {
    width: 360px;
    display: block;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__wrap {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__btn {
    padding: 7.5px 15px;
    span {
      font-size: 12px;
    }
  }
  &__basket {
    position: relative;
    width: 29px;
    &:after {
      content: '';
      position: absolute;
      top: 2px;
      right: -3px;
      width: 8px;
      height: 8px;
      background: #FFAF58;
      border-radius: 50%;
    }
    &:hover {
      svg path {
        fill: #FFAF58;
      }
    }
    svg {
      width: 100%;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
  &__phone {
    display: none;
  }
}
.header-bottom {
  padding: 22px 0;
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    li a {
      font-size: 14px;
      line-height: 120%;
      letter-spacing: 0.2px;
      text-transform: uppercase;
      &:hover {
        color: #FFAF58;
      }
    }
  }
}
.search {
  position: relative;
  input {
    width: 0;
    height: 37px;
    background: transparent;
    border: none;
    padding-right: 22px;
    border-bottom: 1px solid #fff;
    font-size: 16px;
    line-height: 120%;
    color: #fff;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s;
    &:focus {
      border-color: #B88C5E;
    }
  }
  input:focus ~ .search-title,
  input ~ .search-title--active {
    font-size: 12px;
    margin-top: -22px;
  }
  &-title {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    font-size: 16px;
    line-height: 120%;
    color: #fff;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &-btn {
    position: absolute;
    z-index: 2;
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    width: 22px;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &--active {
    input {
      width: 230px;
      padding-right: 30px;
      opacity: 1;
      pointer-events: auto;
    }
    .search-title {
      opacity: 1;
    }
  }
}
.burger {
  position: relative;
  width: 22px;
  height: 16px;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 1.5px;
    background: #fff;
    transition: all 0.3s;
    &:nth-child(1) {
      top: 0;
    }
    &:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    &:nth-child(3) {
      bottom: 0;
    }
  }
  &--active {
    span {
      &:nth-child(1) {
        top: 7px;
        transform: rotate(-45deg);
      }
      &:nth-child(2) {
        display: none;
      }
      &:nth-child(3) {
        bottom: 7px;
        transform: rotate(45deg);
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header-top {
    &__list {
      gap: 16px;
    }
    &__time {
      padding-right: 8px;
      margin-right: 8px;
    }
    &__phone {
      margin-right: 16px;
    }
    &__socials {
      margin-right: 16px;
    }
  }
  .header-bottom {
    &__list {
      li a {
        font-size: 13px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    position: fixed;
    z-index: 15;
    top: 0;
    left: 0;
    width: 100%;
  }
  .header-top {
    position: fixed;
    top: 0;
    left: -105%;
    width: 100%;
    height: 100%;
    padding: 77px 15px 50px;
    transition: all 0.3s;
    overflow-y: auto;
    &--active {
      left: 0;
    }
    .container {
      padding: 0;
    }
    &__inner {
      flex-direction: column;
      align-items: start;
      gap: 24px;
    }
    &__list {
      flex-direction: column;
      align-items: start;
      gap: 24px;
    }
    &__group {
      width: 100%;
      flex-direction: column;
      gap: 24px;
    }
    &__time {
      padding-right: 0;
      margin-right: 0;
      &:after {
        display: none;
      }
    }
    &__phone {
      margin-right: 0;
    }
    &__socials {
      margin-right: 0;
    }
    .header-bottom__list {
      width: calc(100% + 30px);
      display: flex;
      flex-direction: column;
      align-items: start;
      gap: 24px;
      background: #fff;
      padding: 24px 15px;
      margin-left: -15px;
    }
    .search {
      display: block;
    }
    .header-center__btn {
      width: 100%;
      display: flex;
    }
  }
  .header-center {
    position: relative;
    z-index: 15;
    &__logo {
      width: 300px;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__group {
      gap: 16px;
    }
    .header-center__btn {
      display: none;
    }
    .search {
      display: none;
    }
    &__phone {
      width: 18px;
      display: block;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  .header-bottom {
    display: none;
  }
  .search {
    width: 100%;
    padding-left: 30px;
    input {
      width: 100%;
      padding-right: 0;
      opacity: 1;
      pointer-events: auto;
    }
    &-title {
      left: 30px;
      opacity: 1;
      pointer-events: auto;
    }
    &-btn {
      left: 0;
      right: unset;
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header-top {
    padding-top: 70px;
  }
  .header-center {
    &__logo {
      width: 250px;
    }
  }
}

@media screen and (max-width: 450px) {
  .header-top {
    padding-top: 64px;
  }
  .header-center {
    &__logo {
      width: 200px;
    }
  }
}

@media screen and (max-width: 359px) {
  .header-center {
    &__logo {
      width: 160px;
    }
  }
}