.product-main {
  padding: 25px 0 50px;
  &__top {
    display: flex;
    justify-content: space-between;
    gap: 10px;
  }
  .share {
    width: 40px;
    height: 40px;
    flex-shrink: 0;
    background: #000;
    border-radius: 50%;
    svg {
      width: 22px;
      height: auto;
    }
  }
  &__wrap {
    display: flex;
    align-items: start;
    gap: 25px;
    margin-top: 50px;
  }
  .gallery {
    width: 66%;
  }
  &__desc {
    width: 34%;
  }
  .gallery__big {
    .swiper-slide {
      padding-bottom: 78%;
    }
  }
  .gallery__small-wrapp {
    margin-top: 22px;
  }
  .gallery__small {
    .swiper-slide {
      width: calc(100% / 5 - 19px) !important;
      padding-bottom: 17.5%;
    }
  }
  .swiper-slide {
    position: relative;
    border: 1px solid #D9D9D9;
    padding: 5px;
    &-thumb-active {
      border-color: #B88C5E;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  .swiper-button-prev {
    left: 20px;
    &:after {
      font-size: 30px;
      color: #111111;
    }
  }
  .swiper-button-next {
    right: 20px;
    &:after {
      font-size: 30px;
      color: #111111;
    }
  }
  &__group {
    background: #F9EEE2;
    padding: 30px;
  }
  &__price {
    span:nth-child(1) {
      font-size: 35px;
      line-height: 120%;
      font-weight: 600;
    }
    span:nth-child(2) {
      font-size: 25px;
      line-height: 120%;
      color: #999999;
      text-decoration: line-through;
      margin-left: 10px;
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 12px / 2));
    gap: 12px;
    margin-top: 22px;
  }
  &__btn {
    padding: 11.5px 16px;
    span {
      font-size: 14px;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 20px;
    li {
      font-size: 14px;
      line-height: 120%;
      strong {
        font-weight: 600;
      }
      a {
        text-decoration: underline;
        text-decoration-color: #B88C5E;
        text-underline-offset: 3px;
        &:hover {
          text-decoration-color: transparent;
        }
      }
    }
  }
  &__group2 {
    display: flex;
    align-items: center;
    gap: 24px;
    border: 1px solid #D9D9D9;
    padding: 15px;
    margin-top: 20px;
  }
  &__icon {
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F9EEE2;
    border-radius: 50%;
    img {
      max-width: 17px;
    }
  }
  &__list2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
    li {
      position: relative;
      font-size: 14px;
      line-height: 120%;
      padding-left: 12px;
      &:after {
        content: '';
        position: absolute;
        top: 8px;
        left: 0;
        width: 2px;
        height: 2px;
        background: #111111;
        border-radius: 50%;
      }
    }
  }
  .item {
    margin-top: 20px;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
    }
    &-show--active {
      .item-btn {
        transform: rotate(-180deg);
      }
    }
    &-title {
      font-size: 14px;
      line-height: 120%;
      font-weight: 700;
    }
    &-btn {
      width: 6px;
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
    &-hidden {
      margin-top: 10px;
    }
    .product-main__list2 {
      padding-left: 10px;
    }
  }
  &__link {
    display: block;
    font-size: 14px;
    line-height: 120%;
    text-decoration: underline;
    text-decoration-color: #B88C5E;
    text-underline-offset: 3px;
    margin-top: 10px;
    &:hover {
      text-decoration-color: transparent;
    }
  }
}
.branding {
  padding: 50px 0 30px;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 78px / 4));
    gap: 26px;
    margin-top: 30px;
  }
}
.branding-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: #F9EEE2;
  padding: 36px;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - 24px);
    height: calc(100% - 24px);
    border: 1px solid rgba(0, 0, 0, 0.12);
    pointer-events: none;
  }
  &__img {
    position: relative;
    max-width: 108px;
    width: 100%;
    height: 158px;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    font-size: 14px;
    line-height: 140%;
    font-weight: 500;
    margin-top: 16px;
    margin-bottom: 20px;
    span {
      display: block;
      font-size: 12px;
      line-height: 120%;
      margin-top: 10px;
    }
  }
  &__title2 {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 19px;
    line-height: 160%;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  &__btn {
    width: 100%;
    padding: 12.5px;
    margin-top: auto;
    &:hover {
      i path {
        fill: #DEB182;
      }
    }
    span {
      font-size: 12px;
    }
    i {
      width: 13px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
        path {
          transition: all 0.3s;
        }
      }
    }
  }
}
.product-info {
  padding: 30px 0;
  &__group {
    max-width: 945px;
    h2 {
      margin-bottom: 30px;
    }
    h3 {
      font-size: 20px;
      line-height: 130%;
      font-weight: 600;
      letter-spacing: 0.5px;
      text-transform: uppercase;
      margin-bottom: 10px;
    }
    p {
      font-size: 14px;
      line-height: 160%;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
      &:nth-child(3) ~ p {
        display: none;
      }
    }
  }
  .more-text {
    p {
      display: block !important;
    }
  }
  .open-more {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    margin-top: 10px;
    span {
      font-size: 14px;
      line-height: 120%;
      color: #B88C5E;
    }
    span:nth-child(2) {
      display: none;
    }
    i {
      width: 6px;
      display: flex;
      align-items: center;
      transition: .3s;
      img {
        width: 100%;
      }
    }
    &--active {
      span:nth-child(1) {
        display: none;
      }
      span:nth-child(2) {
        display: block;
      }
      i {
        transform: rotate(-180deg);
      }
    }
  }
}
.characteristic {
  padding: 30px 0;
  &__nav {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 30px;
    margin-top: 30px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    li {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 15px;
      font-size: 16px;
      line-height: 120%;
      border-bottom: 1px solid #E9E9E9;
      padding: 10px 0;
      span:nth-child(2) {
        font-weight: 300;
      }
    }
  }
}
.reviews {
  padding: 30px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 30px;
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    gap: 4px 16px;
  }
  &__slider-btns {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  .swiper-button-prev {
    position: static;
    &:after {
      font-size: 30px;
      color: #111111;
    }
  }
  .swiper-button-next {
    position: static;
    &:after {
      font-size: 30px;
      color: #111111;
    }
  }
  .swiper-pagination {
    display: none;
  }
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
}
.review-card {
  height: auto;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.12);
  padding: 30px;
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-bottom: auto;
    p {
      font-size: 14px;
      line-height: 160%;
    }
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 12px;
    margin-top: 32px;
    margin-bottom: auto;
  }
  &__images {
    flex-grow: 1;
    display: grid;
    grid-template-columns: repeat(5, calc(20% - 48px / 5));
    gap: 12px;
  }
  &__img {
    position: relative;
    padding-bottom: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__quantity {
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F9EEE2;
    border-radius: 50%;
    font-size: 18px;
    line-height: 105%;
  }
  &__group2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    margin-top: 32px;
  }
  &__user {
    display: flex;
    align-items: center;
    gap: 10px;
    &-img {
      width: 44px;
      height: 44px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #F9EEE2;
      border-radius: 50%;
      img {
        max-width: 27px;
      }
    }
    &-name {
      font-size: 16px;
      line-height: 120%;
    }
    &-post {
      font-size: 13px;
      line-height: 120%;
      color: #9E9E9E;
      margin-top: 5px;
    }
  }
  .estimation {
    display: flex;
    align-items: center;
    gap: 8px;
    &-number {
      font-size: 16px;
      line-height: 105%;
    }
  }
  .stars {
    display: flex;
    align-items: center;
    gap: 6px;
  }
  .star {
    width: 14px;
    svg {
      width: 100%;
      height: auto;
    }
  }
}



@media screen and (max-width: 1200px) {
  .product-main {
    .gallery {
      width: 55%;
    }
    &__desc {
      width: 45%;
    }
  }
}

@media screen and (max-width: 992px) {
  .product-main {
    padding: 15px 0 25px;
    &__wrap {
      gap: 15px;
      margin-top: 15px;
    }
    .gallery__small {
      .swiper-slide {
        width: 148px !important;
        height: 148px;
        padding-bottom: 0;
        img {
          position: static;
        }
      }
    }
    &__group {
      padding: 18px;
    }
    &__price {
      span:nth-child(1) {
        font-size: 26px;
      }
      span:nth-child(2) {
        font-size: 18px;
      }
    }
    @media screen and (min-width: 769px) {
      .product-main__btns {
        grid-template-columns: 100%;
      }
    }
  }
  .branding {
    padding: 25px 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 10px;
      margin-top: 20px;
    }
  }
  .branding-card {
    flex-direction: row;
    gap: 16px;
    padding: 24px;
    &:after {
      width: calc(100% - 14px);
      height: calc(100% - 14px);
    }
    &__img {
      max-width: 70px;
      height: 104px;
      flex-shrink: 0;
    }
    &__desc {
      align-items: start;
      text-align: start;
    }
    &__title {
      margin-top: 0;
      margin-bottom: 10px;
      span {
        margin-top: 0;
      }
    }
    &__btn {
      width: auto;
      margin-right: auto;
    }
  }
  .product-info {
    padding: 25px 0;
    &__group {
      h2 {
        margin-bottom: 20px;
      }
    }
  }
  .characteristic {
    padding: 25px 0;
    &__nav {
      grid-template-columns: 100%;
      gap: 0;
      margin-top: 20px;
    }
    &__list {
      li {
        font-size: 14px;
      }
    }
  }
  .reviews {
    padding: 25px 0;
    &__slider {
      padding-bottom: 30px;
    }
    .swiper {
      overflow: unset;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      top: unset;
      bottom: 0;
      display: block;
    }
  }
  .review-card {
    padding: 16px;
    &__text {
      gap: 10px;
    }
    &__group {
      margin-top: 24px;
    }
    &__group2 {
      flex-direction: column;
      align-items: start;
      gap: 12px;
      margin-top: 24px;
    }
    &__quantity {
      width: 70px;
      height: 70px;
    }
    &__user {
      &-name {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .product-main {
    &__wrap {
      flex-direction: column;
    }
    .gallery {
      width: 100%;
    }
    &__desc {
      width: 100%;
    }
  }
  .branding {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .review-card {
    &__images {
      grid-template-columns: repeat(2, calc(50% - 12px / 2));
    }
    &__img {
      &:nth-child(2) ~ .review-card__img {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .product-main {
    .swiper-button-prev {
      left: 10px;
    }
    .swiper-button-next {
      right: 10px;
    }
    &__btns {
      display: flex;
      align-items: stretch;
    }
    &__btn {
      padding: 11.5px 22px;
    }
  }
}

@media screen and (max-width: 359px) {
  .product-main {
    &__btns {
      display: grid;
      grid-template-columns: 100%;
    }
  }
}